import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { BusyService } from '@core';
import { environment } from '@environment';

@Injectable({ providedIn: 'root' })
export class MigrationTestService {

    constructor(
      private http: HttpClient,
      private busyService: BusyService
    ) { }

    /**
     * Tell server to migrate an account to SurePayroll
     * @param companyId Id of company of account to migrate
     * @returns Cold, terminating observable of migration result.
     * You must subscribe but do not have to unsubscribe.
     */
    migrate(companyId: string): Observable<SpMigrationResponse> {
      const eurl = environment.apiRoot + `/enrollment/finalize`;
      const body = { companyId };
      return this.busyService.busy$('Migrating ...',
        this.http.post<SpMigrationResponse>(eurl, body)
      );
    }
}

export interface SpMigrationResponse {
  success: boolean;
  validationErrors: string[]; // integer
}
