import { Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import { AccountStatus, Company } from '@model';
import { SpMigrationResponse } from './migration-test-service';

@Component({
  selector: 'sp-admin-account-view-migrate',
  templateUrl: './admin-account-view-migrate.component.html'
})
export class AdminAccountViewMigrateComponent {
  @Input() company: Company;
  @Input() migrationResult: SpMigrationResponse;
  @Input() readOnly = true;
  @Output() migrateToSp = new EventEmitter<Company>();
  @ViewChild('confirmMigrationToSp') confirmMigrationToSp: ElementRef;

  constructor(private modalService: NgbModal) { }

  get canMigrateToSp() {
    return !this.readOnly && this.company != null && this.company.status === AccountStatus.Operations;
  }

  async confirmMigrateToSp() {
    this.migrationResult = null;
    const confirmed = await this.modalService
      .open(this.confirmMigrationToSp)
      .result
      .then(result => result === 'ok', () => false);
    if (confirmed) {
      this.migrateToSp.emit(this.company);
    }
  }

  get migrateResultClass() {
    return this.migrationResult
      ? this.migrationResult.success
        ? 'badge badge badge-pill badge-success'
        : 'badge badge badge-pill badge-danger'
      : null;
  }
}
