import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';

import { SharedModule } from '@app/shared/shared.module';
import { AccountDetailsModule } from '@app/account-details';

import { AdminAccountListComponent } from './admin-account-list.component';
import { AdminAccountListDetailComponent } from './admin-account-list-detail.component';

import { AdminAccountViewContainerComponent } from './admin-account-view/admin-account-view-container.component';
import { AdminAccountViewErrorsComponent } from './admin-account-view/admin-account-view-errors.component';
import { AdminAccountViewMigrateComponent } from './admin-account-view/admin-account-view-migrate.component';
import { AdminAccountViewStatusComponent } from './admin-account-view/admin-account-view-status.component';

import { AdminAccountViewGuard } from './admin-account-view/admin-account-view-guard';

const routes: Routes = [
  {
    path: '',
    pathMatch: 'full',
    component: AdminAccountListComponent,
    data: { title: 'Admin' }
  },
  {
    // Ex: ~/admin/account-view/14869218ca0645cea77d491325216e46/76d58284c1a04e3eb8080162c1bc45a9
    path: 'account-view/:companyId/:userId',
    canActivate: [AdminAccountViewGuard],
    component: AdminAccountViewContainerComponent,
    data: { title: 'AdminAcctView' }
  },
  {
    // Ex: '~/admin/account-view'
    path: 'account-view',
    pathMatch: 'full',
    canActivate: [AdminAccountViewGuard],
    component: AdminAccountViewContainerComponent,
    data: { title: 'AdminAcctView' }
  },
  { path: '**', pathMatch: 'prefix', redirectTo: '' }
];

@NgModule({
  imports: [
    SharedModule,
    AccountDetailsModule,
    NgxDatatableModule,
    RouterModule.forChild(routes)
  ],
  declarations: [
    AdminAccountListComponent,
    AdminAccountListDetailComponent,

    AdminAccountViewContainerComponent,
    AdminAccountViewErrorsComponent,
    AdminAccountViewMigrateComponent,
    AdminAccountViewStatusComponent,
  ]
})
export class AdminConsoleModule {}
