import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';

import { EMPTY, Observable, of, combineLatest, concat } from 'rxjs';
import { concatMap, first, map, switchMap, tap, filter } from 'rxjs/operators';

import { AccountStatus, AccountStatusCodes, Company, User } from '@model';
import { AccountStatusVm } from './account-status-vm';
import { DocumentPackageService, DocumentPackage } from '@entity/entity-services';
import { environment } from '@environment';
import { EsignService, WellKnownRoutes } from '@core';
import { lowerCamelCase, Indexable } from '@core/utils';
import { MigrationTestService, SpMigrationResponse } from './migration-test-service';
import { ReviewService } from '@entity';
import { SessionService } from '@session';
import { StateTransitionRulesResults } from './state-transition-rules-results';
import { ValidationService, isValidatorGroupConfig } from '@validation';

@Component({
  selector: 'sp-admin-account-view-container',
  templateUrl: './admin-account-view-container.component.html'
})
export class AdminAccountViewContainerComponent implements OnInit {
  back = WellKnownRoutes.Admin;
  review = WellKnownRoutes.Review;

  company$: Observable<Company>;
  enableMigration = environment.enableMigration;
  migrationResult: SpMigrationResponse;
  readOnly$: Observable<boolean>;
  stateTransitionRulesResults$: Observable<StateTransitionRulesResults>;
  statusVm$: Observable<AccountStatusVm>;
  documentPackage: DocumentPackage;

  constructor(
    private documentPackageService: DocumentPackageService,
    private esignService: EsignService,
    private migrationService: MigrationTestService,
    private reviewService: ReviewService,
    private sessionService: SessionService,
    private validationService: ValidationService
  ) { }

  ngOnInit(): void {
    const accountStateTransitions$ = this.sessionService.accountStateTransitions$;
    this.company$ = this.sessionService.currentCompany$;
    this.readOnly$ = this.sessionService.readOnly$;
    this.documentPackageService.loadDocumentPackage().subscribe(dp => this.documentPackage = dp);

    const setStatus = ((newStatus: AccountStatus) => {
        return this.sessionService.setAccountStatus(newStatus).pipe(
          // Load docs after loading company package; don't care when it finishes
          tap(_ => this.documentPackageService.loadDocumentPackage().subscribe(dp => this.documentPackage = dp) )
        );
    }).bind(this);

    this.statusVm$ = combineLatest(
      accountStateTransitions$,
      this.company$
    ).pipe(
      // TODO remove filter after feat-current-company-user-never-null is merged, commit 4336857a4469dfa9e54d94509ac3dd633ff42e67
      filter(([accountStateTransitions, company]) => !!company),
      map(([accountStateTransitions, company]) => {
        if (!company) {
          return null;
        }
        const currentStatus = company.status;
        const transitions = accountStateTransitions[currentStatus] || {};
        const statusCodes = [currentStatus, ...Object.keys(transitions)].filter(a => {
          return a === currentStatus || !transitions[a].hideInUi;
        })  as AccountStatus[];
        return { currentStatus, statusCodes, setStatus };
      })
    );

    this.stateTransitionRulesResults$ = combineLatest(
      accountStateTransitions$,
      this.company$,
      this.documentPackageService.documentPackage$, // need to initialize and after forced signage
      this.reviewService.isValid$,
    ).pipe(
      // TODO remove filter after feat-current-company-user-never-null is merged, commit 4336857a4469dfa9e54d94509ac3dd633ff42e67
      filter(([accountStateTransitions, company, docPackage, isValid]) => !!company),
      switchMap(([accountStateTransitions, company, docPackage, isValid]) => {
        if (!company) {
          return of(null);
        }
        const currentStatus = company.status;
        const nextTransitions = accountStateTransitions[currentStatus] || {};

        // Run all applicable state transition validators
        const validatorGroups = Object.keys(nextTransitions).map(next => `transition-${lowerCamelCase(next)}`);

        const validationResult$ = this.validationService.validateEntityCache(validator => {
          return validator.group && validatorGroups.some(group => validator.group === group);
        });

        return validationResult$.pipe(
          map(result => {
            const validationErrors = result.validationErrors || [];
            const transitionRulesErrors = Object.keys(nextTransitions).reduce((acc, grp) => {
              const group = `transition-${lowerCamelCase(grp)}`;
              const errors = validationErrors
                .filter(ve => ve.validator.group === group)
                .map(ve => ve.getErrorMessage());
              if (errors.length) {
                acc[grp] = errors;
              }
              return acc;
            }, {} as Indexable<string[]>);

            const nextStatusesWithErrors = Object.keys(transitionRulesErrors);

            return {
              hasValidationErrors: !isValid,
              nextStatusesWithErrors,
              transitionRulesErrors,
            };
          })
        );
      })
    );
  }

  completeDocs(company: Company) {
    this.esignService.markEnvelopesSigned(company.id).pipe(
      concatMap(changed =>
        changed ? this.documentPackageService.loadDocumentPackage() : EMPTY
      )
    ).subscribe(dp => { if (dp) { this.documentPackage = dp; }});
  }

  migrateToSp(company: Company) {
    this.migrationResult = null;
    this.migrationService.migrate(company.id).pipe(
      tap(r => this.migrationResult = r),
      concatMap(_ => this.sessionService.reloadCurrentCompanyPackage())
    ).subscribe();
  }
}
