import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, CanActivateChild, Router, RouterStateSnapshot } from '@angular/router';

import { Observable, of } from 'rxjs';
import { catchError, concatMap, first, take, tap } from 'rxjs/operators';

import { AuthService } from '@core/services/auth-services/auth.service';
import { SessionService } from '@session';
import { WellKnownRoutes } from '@core';

/**
 * Guard a page that must be authenticated but is not tied to the Current Account
 */
@Injectable({ providedIn: 'root' })
export class AdminAccountViewGuard {

  constructor(
    private authService: AuthService,
    private sessionService: SessionService,
    public router: Router
  ) { }

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    const companyId = route.paramMap.get('companyId');
    const userId = route.paramMap.get('userId');

    return this.sessionService.isAdmin$.pipe(
      concatMap(isAdmin => {
        if (isAdmin) {
          return companyId
            ? this.loadCompanyPackageByCompanyUserIds(companyId, userId)
            : this.activateWithLoadedCompanyPackage();
        } else {
          // Uh oh. Non-admin shouldn't be here. Try going to dashboard
          this.logEvent('not admin; reroute to dashboard');
          this.router.navigate([WellKnownRoutes.Dashboard]);
          return of(false);
        }
      }),
      take(1) // ensure terminates
    );
  }

  /**
   * No companyId.
   * Can activate with current package if loaded
   * else don't activate and navigate away instead.
   * Ex: '~/admin/account-view'
   */
  private activateWithLoadedCompanyPackage():  Observable<boolean> {
    return this.sessionService.companyPackageLoaded$.pipe(
      first(),
      tap(isLoaded => {
       if (!isLoaded) {
          // Uh oh. Should be loaded. Re-route to account list so admin user can pick
          this.logEvent('no company id and not loaded; reroute to admin account list');
          this.router.navigate([WellKnownRoutes.Admin]);
        } else {
          this.logEvent('activating account-view for current company package');
        }
      })
    );
  }

  /**
   * Specified companyId
   * Load the CompanyPackage for the specified company and user
   *
   * Ex: ~/admin/account-view/14869218ca0645cea77d491325216e46/76d58284c1a04e3eb8080162c1bc45a9
   *
   * @returns A COLD, terminating boolean observable.
   * You must subscribe to make it work.
   * You do not have to unsubscribe.
   * Emits true if succeeded, false if failed.
   */
  private loadCompanyPackageByCompanyUserIds(companyId: string, userId: string): Observable<boolean> {
    return this.sessionService.loadAccountByCompanyUserIds(companyId, userId).pipe(
      tap(_ => this.logEvent(`loaded company package for ${companyId} and user ${userId || 'none'}`)),
      catchError(error => {
        // Load failed. Assume error already logged. Re-route to admin account list.
        this.logEvent('company package load failed; re-route to admin account list');
        this.router.navigate([WellKnownRoutes.Admin]);
        return of(false);
      })
    );
  }

  /**
   * Log a message about what this guard did
   * when auth tracing is turned on.
   * Typically log a re-routing event.
   */
  private logEvent(message: string) {
    this.authService.logEvent(`AdminAccountViewGuard ${message}`);
  }
}
