/**
 * DO NOT EXPAND THIS VIEWMODEL !
 * Doing so exposes more sensitive details about many accounts.
 * That's bad for privacy and bad for performance.
 * The AccountView purpose is to quickly identity a single account of interest.
 * It is NOT for displaying account details in the list.
 * It is NOT to make it easier for admins to answer questions from the list view
 * They should select and drill into to learn more about a specific account.
 * Obtain executive approval before adding to this VM.
 */

import { Company, User, AccountStatus } from '@model';
import { Indexable } from '@utils';

/**
 * The fields of an individual account list projection from the server
 */
export interface AccountListItem {
  // Company fields
  bcl: string;
  companyId: string;
  dbaName: string;
  legalName: string;
  startDate: string; // based on Company.enrollmentStartDate ISO string
  status: string;

  // User fields
  email: string;
  firstName: string;
  lastName: string;
  phone: string;
  userId: string;
  username: string;
}

/**
 * ViewModel of server-provided AccountList item
 */
export interface AdminAccountVm extends AccountListItem {
  /** Id of this VM, constructed from Company and User ids */
  id: string;
  displayName: string; // Legal or Dba
  userFullName: string;
}

/** A page of AccountList VMs for display to admins */
export interface AdminAccountVmPage {
  /** Count of all accounts that satisfy the query criteria */
  count: number;
  /** A page of accountList data */
  rows: AdminAccountVm[];
}

export const defaultFilterStatus = {
  [AccountStatus.Abandoned]: false,
  [AccountStatus.AutoMigrate]: false,
  [AccountStatus.Docs]: false,
  [AccountStatus.Enrolled]: false,
  [AccountStatus.Migrating]: false,
  [AccountStatus.Open]: true,
  [AccountStatus.Operations]: false,
  [AccountStatus.Test]: false,
};
export type FilterStatus = typeof defaultFilterStatus;

/** All admin console filter and paging criteria */
export interface FilterData {
  /** Limit accounts to those with status:true in this object. If all are false, do not filter by status. */
  status: FilterStatus;
  /** User's current filter string. If empty, do not filter by text. */
  text: string;
  /** Paging constraints */
  page: PageData;
}

/** Grid paging data */
export interface PageData {
  pageSize: number;
  pageNumber: number;
  totalRows: number;
  orderBy: string;
  orderDir: 'asc'|'desc';
}

/** Criteria sent to the server */
export interface FilterQuery extends Indexable {
  status: string;
  text: string;
  pageSize: number;
  pageNumber: number;
  orderBy: string;
  orderDir: 'asc'|'desc';
}
