import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { AccountListItem, AdminAccountVm, AdminAccountVmPage, FilterQuery } from './admin-account-vm';
import { environment } from '@environment';
import { SessionService } from '@session';

interface AccountListResponse {
  /** Count of all accounts that satisfy the query criteria */
  count: number;
  /** A page of accountList item data */
  rows: AccountListItem[];
}

const accountListUrl = `${environment.apiRoot}/admin/accountquery`;

@Injectable({ providedIn: 'root' })
export class AdminConsoleService {

  constructor(private http: HttpClient, private sessionService: SessionService) { }

  /**
   * Get a page of filtered, sorted data
   * @returns Observable of
   */
  getAccountList(query: FilterQuery): Observable<AdminAccountVmPage> {
    this.sessionService.clearCurrentAccount();

    return this.http.get<AccountListResponse>(accountListUrl, { params: query }).pipe(
      map(data => {
        // assert action data has the right shape
        const responseData = assertExpectedAccountListResponse(data);
        const page = this.makeAccountList(responseData);
        return page;
      }),
      catchError(error => {
        // No real error handling.
        const message = `Failed to get Account List for query ${JSON.stringify(query)}`;
        this.sessionService.logger.error(message, error);
        return throwError(message);
      })
    );
  }

  /**
   * Load the account specified in the selected AdminAccountVm
   * @returns Terminating boolean observable. Must subscribe; need not unsubscribe.
   */
  loadSelectedAccount(vm: AdminAccountVm) {
    return this.sessionService.loadAccountByCompanyUserIds(vm.companyId, vm.userId);
  }

  private makeAccountList(data: AccountListResponse): AdminAccountVmPage {
    const vms = data.rows.map(d => {
      const vm = d as AdminAccountVm;
      // Fill in AdminAccountVm extended fields
      vm.id = d.companyId + '/' + d.userId;
      vm.displayName = d.legalName || d.dbaName;
      vm.userFullName = d.firstName + ' ' + d.lastName;
      return vm;
    });

    return {
      count: data.count,
      rows: vms
    };
  }
}

function assertExpectedAccountListResponse(data: AccountListResponse): AccountListResponse {
  if (Number.isInteger(data.count) && Array.isArray(data.rows)) {
    return data;
  } else if (environment.production) {
    return { count: 0, rows: [] };
  } else {
    throw new Error('Unexpected Account List data from server');
  }
}
