import { Component, EventEmitter, Input, Output} from '@angular/core';

import { AccountStatus, Company } from '@model';
import { StateTransitionRulesResults } from './state-transition-rules-results';
import { WellKnownRoutes } from '@core';
import { DocumentPackage } from '@entity';

@Component({
  selector: 'sp-admin-account-view-errors',
  templateUrl: './admin-account-view-errors.component.html'
})
export class AdminAccountViewErrorsComponent {
  @Input() company: Company;
  @Input() documentPackage: DocumentPackage;
  @Output() completeDocs = new EventEmitter<Company>();
  @Input() results: StateTransitionRulesResults;
  @Input() readOnly = true;
  review = WellKnownRoutes.Review;

  markDocsComplete() {
    this.completeDocs.emit(this.company);
  }
  get canMarkDocsComplete() {
    const isWetSign = this.documentPackage && this.documentPackage.Envelope.some(e => e.isWetSign);

    return !this.readOnly && this.company != null && this.company.status === AccountStatus.Operations && isWetSign;
  }
}
