import { Component, Input } from '@angular/core';
import { AdminAccountVm } from './admin-account-vm';

@Component({
  selector: 'sp-admin-account-list-detail',
  templateUrl: './admin-account-list-detail.component.html'
})
export class AdminAccountListDetailComponent {
  @Input() vm: AdminAccountVm;
  get userIdShort() { return this.vm ? this.vm.userId.substr(0, 6) : ''; }
  get companyIdShort() { return this.vm ? this.vm.companyId.substr(0, 6) : ''; }
}
