import { Component, ElementRef, Input, ViewChild } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

import {finalize } from 'rxjs/operators';

import { AccountStatus, Company} from '@model';
import { AccountStatusVm } from './account-status-vm';
import { LoggerService } from '@core/services/logger.service';

@Component({
  selector: 'sp-admin-account-view-status',
  templateUrl: './admin-account-view-status.component.html'
})
export class AdminAccountViewStatusComponent {

  error: string;

  @Input() company: Company;
  @Input() readOnly = true;
  @Input() statusVm: AccountStatusVm;

  @ViewChild('confirmSetStatusContent')
  confirmSetStatusContent: ElementRef;

  @ViewChild('statusSelect')
  statusSelect: ElementRef;

  newStatus: AccountStatus;
  setStatusPending = false;

  constructor(private logger: LoggerService, private modalService: NgbModal) { }

  async confirmStatusChange(newStatus: AccountStatus) {
    const currentStatus = this.statusVm.currentStatus;
    if (this.readOnly) {
      // revert if ReadOnly. Shouldn't even be called
      this.statusSelect.nativeElement.value = currentStatus;
      return;
    }
    if (newStatus === currentStatus) {
      return;
    }
    this.error = '';
    // automatically confirmed unless changing to Enrolled or Migrating
    let confirmed = newStatus !== AccountStatus.Enrolled && newStatus !== AccountStatus.Migrating;

    if (!confirmed) {
      this.newStatus = newStatus;
      confirmed = await this.modalService
        .open(this.confirmSetStatusContent)
        .result.then(result => result === 'ok', () => false);
    }

    if (confirmed) {
      this.setStatusPending = true;
      this.statusVm.setStatus(newStatus).pipe(
        finalize(() => this.setStatusPending = false)
      ).subscribe(
        ar => {
          if (ar && ar.length) {
            this.error = 'Server-side transition errors: ' + ar.join('; ');
          }
        },
        // Status change failed; revert the selectInput's status value
        error => {
          const message =
            `Error changing status from "${currentStatus}" to "${newStatus}" for BCL ${this.company.bcl}.`;
          this.logger.error(message, error);
          this.error = (error.error && error.error.message) || message + ' Check console.';
          this.statusSelect.nativeElement.value = currentStatus;
        }
      );
    } else {
      // Canceled status change; revert
      this.statusSelect.nativeElement.value = currentStatus;
    }
  }

  get isTerminalStatus(): boolean {
    return this.statusVm.currentStatus === AccountStatus.Enrolled;
  }
}
