import {
  Component, OnInit, AfterViewInit, ElementRef, EventEmitter, Input, Output, ViewChild
} from '@angular/core';
import { DatePipe } from '@angular/common';

import { merge, Observable, of, ReplaySubject} from 'rxjs';
import { debounceTime, map, switchMap, take, catchError } from 'rxjs/operators';

import { AdminAccountVm, defaultFilterStatus, FilterData, FilterStatus, PageData, FilterQuery } from './admin-account-vm';
import { AccountStatus } from '@model';

import { PhonePipe } from '@shared/pipes/phone.pipe';
import { AdminConsoleService } from './admin-console.service';
import { Router } from '@angular/router';
import { WellKnownRoutes } from '@app/core';
import { ngIfAnim } from '@animations';
import { DatatableComponent } from '@swimlane/ngx-datatable';

const _datePipe = new DatePipe('en-US');
export const filterDataKey = 'admin-filter-data';

@Component({
  selector: 'sp-admin-account-list',
  templateUrl: './admin-account-list.component.html',
  animations: [ngIfAnim]
})
export class AdminAccountListComponent implements OnInit, AfterViewInit {
  @Output() goto$ = new EventEmitter();
  @Output() gotoOverview$ = new EventEmitter();
  @ViewChild('accountListTable') table: DatatableComponent;
  @ViewChild('textFilter') textFilter: ElementRef;

  /** Save FilterData in browser storage if available */
  static saveFilterData = !!window.localStorage;

  /** Time in ms before acting on user input */
  static debounceDelay = 600;

  AccountStatus = AccountStatus;
  error: string;

  datePipe = { transform: (value: string) => _datePipe.transform(value, 'shortDate') };
  companyNamePipe = { transform: (value: string) => value || '<i>No company name</i>' };
  userPipe = { transform: (value: string) => value || '<i>No user name</i>' };

  /** Limit accounts to those with status:true in this object. If all are false, do not filter by status. */
  filterStatus = { ...defaultFilterStatus };

  /** User's current filter string. If empty, do not filter by text. */
  filterText = '';

  page: PageData = {
    totalRows: 0,
    orderBy: 'startDate',
    orderDir: 'desc',
    pageNumber: 0,
    pageSize: 20
  };

  private applyFiltersSubject = new ReplaySubject(1); // apply immediately
  private filterChangeSubject = new ReplaySubject(1); // debounce filter changes, then apply

  /** Filtered rows of admin account ViewModels to display */
  rows$ = merge(
      this.applyFiltersSubject,
      this.filterChangeSubject.pipe(debounceTime(AdminAccountListComponent.debounceDelay))
    )
    .pipe(
      map(() => this.makeFilterData()),
      switchMap(filterData => {
        this.saveFilterDataToStorage(filterData);
        return this.applyFilters(filterData);
      })
    );

  /** Function returning the unique id of an AdminAccountVm */
  rowIdentity = (row: AdminAccountVm) => row.id;

  constructor(private adminConsoleService: AdminConsoleService, private router: Router) {}

  ngOnInit() {
    // Update from filter data storage (if available)
    this.getFilterDataFromStorage();
  }

  // Wait until rows$ is bound and textFilter appears
  ngAfterViewInit() {
    // Apply current state of filters when first known
    this.applyFiltersSubject.next();
    this.textFilter.nativeElement.focus();
  }

  private applyFilters(filterData: FilterData): Observable<AdminAccountVm[]> {
    const status = this.getStatusFilterAsString(filterData.status);
    const text = filterData.text ? filterData.text.trim().toLowerCase() : '';
    const { pageSize, pageNumber, orderBy, orderDir } = filterData.page;
    const query: FilterQuery = { pageSize, pageNumber, orderBy, orderDir, status, text };

    return this.adminConsoleService.getAccountList(query).pipe(
      map(response => {
        this.page.totalRows = response.count;
        return response.rows;
      }),
      catchError(error => {
        this.error = error.message ? error.message : JSON.stringify(error);
        return [];
      }),
    );
  }

  private getFilterDataFromStorage() {
    if (AdminAccountListComponent.saveFilterData) {
      const rawFilterData = window.localStorage.getItem(filterDataKey);
      if (rawFilterData) {
        try {
          const filterData: FilterData = JSON.parse(rawFilterData);
          this.filterText = filterData.text;
          this.filterStatus = filterData.status;
          if (filterData.page) {
            this.page = filterData.page;
          }
        } catch (error) {
          // uh oh. Clear the bad stuff and keep going with the default filters.
          window.localStorage.clearItem(filterDataKey);
        }
      }
    }
  }

  private getStatusFilterAsString(filterStatus: FilterStatus): string {
    return Object.keys(filterStatus).reduce((acc, key) => {
      const v: boolean = (filterStatus as any)[key];
      return v ? acc + key + '|' : acc;
    }, '');
  }

  private goto(selectedVm: AdminAccountVm, where?: string) {
    where = where || WellKnownRoutes.Dashboard;
    this.adminConsoleService.loadSelectedAccount(selectedVm).subscribe(
        () => this.router.navigate([where]),
        error => (this.error = error.message ? error.message : JSON.stringify(error))
      );
  }

  gotoAccountView(selectedVm: AdminAccountVm) {
    this.goto(selectedVm, WellKnownRoutes.AdminAccountView);
  }

  private makeFilterData(): FilterData {
    return {
      text: this.filterText,
      status: this.filterStatus,
      page: this.page
    };
  }

  onActivate(event: any) {
    // Click anywhere except the expand-detail or gotoAccountView cells (nameless cols)
    if (event.type === 'click' && event.column.name ) {
      this.goto(event.row);
    }
  }

  onDetailToggle(event: any) {
    // console.log('Detail Toggled', event);
  }

  onSelect(event: any) {
    // console.log('Select Event', event.selected);
  }

  pageCallback(pageInfo: { count?: number, pageSize?: number, limit?: number, offset?: number }) {
    if (pageInfo.offset !== this.page.pageNumber) {
      this.page.pageNumber = pageInfo.offset;
      this.applyFiltersSubject.next();
    }
  }

  private saveFilterDataToStorage(filterData: FilterData) {
    if (AdminAccountListComponent.saveFilterData) {
      window.localStorage.setItem(filterDataKey, JSON.stringify(filterData));
    }
  }

  sortCallback(sortInfo: { sorts: { dir: 'asc'|'desc', prop: string }[], column: {}, prevValue: string, newValue: string }) {
    // there will always be one "sort" object if "sortType" is set to "single"
    this.page.orderDir = sortInfo.sorts[0].dir;
    this.page.orderBy = sortInfo.sorts[0].prop;
    this.page.pageNumber = 0; // always start from page 1 when sort changes
    this.applyFiltersSubject.next();
  }

  toggleExpandRow(row: AdminAccountVm) {
    this.table.rowDetail.toggleExpandRow(row);
  }

  updateFilters() {
    this.page.pageNumber = 0;
    this.filterChangeSubject.next();
  }
}
